.menuBar{
    display: inline-block;
    position: relative;
    color: #ffffff;
    text-decoration: none;
  }
  .menuBar:after{
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #ffffff;
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
  }
  .menuBar:hover:after{
    transform: scaleX(1);
    transform-origin: bottom left;
  }

